<template>
    <v-app id="inspire">
        <v-content class="app-content">
            <router-view @redirectRouteCustom="redirectRouteCustom" />
        </v-content>
        <div class="top-layer"></div>
        <div class="top-layer top-layer--2"></div>
        <div class="top-layer top-layer--3"></div>
        <div class="top-layer top-layer--4"></div>
        <div class="top-layer top-layer--5"></div>
        <!-- <audio autoplay id="bg-music" loop="" src="http://themes.pixelwars.org/ikonik-html/audio/music2.mp3" __idm_id__="734364673"></audio> -->
    </v-app>
</template>

<script>
    export default {
        name: 'App',
        components: {
            
        },
        methods: {
            redirectRouteCustom(page) {
                var _this = this
                var layerClass = ".top-layer";
                var layers = document.querySelectorAll(layerClass);
                for (const layer of layers) {
                    layer.classList.toggle("active");
                }
                setTimeout(() => {
                    _this.$router.push(page)
                }, 500)
            }
        },
        mounted() {
            
        },
        updated() {
            
        }
    }
</script>

<style>
    * {
        box-sizing: border-box;
        font-family: "Rubik", sans-serif;
    }
    #app {
        font-family: "Rubik", sans-serif;
        /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
    }
    #inspire {
        padding: 3rem 0;
    }
    html, body, .home--container, #inspire, .application--wrap, .app-content  {
        min-height: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        padding: 0;
        margin: 0;
    }
    .tcd-container {
        margin: 3rem 0 0;
    }
    /* SECTION TITLE */
    .title-container {
        /* width: 200px;
        display: flex; */
        /* align-items: center; */
    }
    .block__header {
        text-align: right;
        padding-right: 3rem;
    }
    .block__title--container {
        position: relative;
        display: inline-block;
        margin-bottom: 1rem;
    }
    .block__title--content {
        font-size: 1.25rem;
        font-weight: bold;
        line-height: 2.5rem;
        color:  #2f2f2f;
        position: relative;
        padding: 0;
        margin: 0;
    }
    .block__title--background {
        color: #e9e9e9;
        position: absolute;
        top: 50%;
        right: 25%;
        font-weight: bold;
        font-size: 2.25rem;
        white-space: nowrap;
        padding: 0;
        margin: 0;
        transform: translate(0%, -50%);
    }
    @media only screen and (max-width: 382px) {
        html,body{ 
            margin:0; 
            padding:0; 
            height:100%; 
            width:100%; 
            overflow: auto;
        }
    }

    .title__container {
        text-align: center;
        margin: 7rem 0 5rem;
        position: relative;
    }
    .title__container .title__content {
        font-family: "Rubik", sans-serif;
        font-size: 3rem;
        font-weight: bold;
        color: #06292f;
        z-index: 2;
        position: relative;
    }
    .title__container .title__background {
        font-family: "Rubik", sans-serif;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 5rem;
        font-weight: bold;
        color: #e9e9e9;
        z-index: 1;
    } 

    .button-close {
        position: fixed;
        top: 0;
        left: 50%;
        width: 55px;
        height: 55px;
        background-color: #06292f;
        transform: translate(-50%,0);
        z-index: 3;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }
    .button-close::before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%,0);
        width: 110%;
        height: 0%;
        background-color: #d2e893;
        transition: all 0.5s;
    }
    .button-close i {
        color: #d2e893!important;
        font-size: 2rem;
    }
    .button-close:hover:before {
        height: 100%;
    }
    .button-close:hover i {
        transform: rotate(360deg);
        color: #06292f!important;
    }

    .top-layer {
        position: absolute;
        width: 100%;
        height: 100%;
        top: -100%;
        left: 0;
        bottom: auto;
        right: auto;
        background: #d2e893;
        -webkit-transition: all 0.9s cubic-bezier(0.6, 0.05, 0.4, 1);
        transition: all 0.9s cubic-bezier(0.6, 0.05, 0.4, 1);
        z-index: 999;
    }
    .top-layer.active {
        top: 100%;
    }
    .top-layer--2 {
        background: #06292f;
        -webkit-transition-delay: 0.6s;
                transition-delay: 0.6s;
    }
    .top-layer--3 {
        background: #d2e893;
        -webkit-transition-delay: 0.4s;
                transition-delay: 0.4s;
    }
    .top-layer--4 {
        background: #06292f;
        -webkit-transition-delay: 0.2s;
                transition-delay: 0.2s;
    }
    .top-layer--5 {
        background: #599e71;
        -webkit-transition-delay: 0.1s;
                transition-delay: 0.1s;
    }
    @media screen and (max-width: 960px) {
        .block__header {
            padding: 0;
            text-align: center;
        }
        .about-us__container .block__header {
            padding-right: 1rem;
        }
        .block__title--background {
            right: 50%;
            transform: translate(50%, -50%);
        }
    }
    @media screen and (max-width: 600px) {
        .tcd-container {
            margin-top: 2rem; 
        }
        .title__container .title__content {
            font-size: 2rem;
        }
        .title__container .title__background {
            font-size: 4rem;
        } 
        .experience__item-content {
            padding-left: 2rem;
            padding-top: 1rem;
        }
        .experience__container .experience__item {
            padding-bottom: 2rem;
        }
    }
    @media (max-width:991px) and (min-width:960px) {
        .home__block {
            max-width: 900px!important;
        }
        .block__header {
            padding-right: 2rem!important;
        }
    }
</style>



