<template>
    <v-layout row wrap class="skills__container tcd-container">
        <v-flex xs12 md4 lg4 class="skills__header block__header">
            <div class="block__title--container">
                <p class="block__title--background">SKILLS</p>
                <p class="block__title--content">SKILLS</p>
            </div>
        </v-flex>
        <v-flex xs12 md8 lg8 class="skills__content">
            <v-layout row>
                <v-flex xs12 sm6 md6 lg6 class="skills__item" v-for="(value, name, index) in skills" v-bind:key="index">
                    <p class="skills__item-label">
                        <span class="skills__item-score">{{value.skillScore}}</span>
                        <span class="skills__item-name">{{value.skillName}}</span>
                    </p>
                    <div class="skills__item-bar">
                        <div class="skills__item-bar-procces"  v-bind:style="{width: value.skillScore}"></div>   
                    </div>
                </v-flex>
            </v-layout>
        </v-flex>
    </v-layout>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
        skills: [
            {
                skillName: "REACT NATIVE",
                skillScore: "50%" 
            },
            {
                skillName: "REACTJS",
                skillScore: "50%" 
            },
            {
                skillName: "VUEJS",
                skillScore: "40%" 
            },
            {
                skillName: "PHP",
                skillScore: "40%" 
            },
            {
                skillName: "LARAVEL",
                skillScore: "40%" 
            },
            {
                skillName: "CRUDBOOSTER",
                skillScore: "50%" 
            },
            {
                skillName: "PRESTASHOP",
                skillScore: "50%" 
            },
            {
                skillName: "HTML/CSS/JAVASCRIPT",
                skillScore: "70%" 
            }
        ]
    };
  }
};
</script>

<style scoped>
    .skills__container .skills__content .row {
        margin: 0;
    }
    .skills__container .skills__item {
        margin-bottom: 2rem;
    }
    .skills__container .skills__item-label {
        font-weight: 700;
    }
    .skills__container .skills__item-label .skills__item-score {
        color: #59a073;
    }
    .skills__container .skills__item-label .skills__item-name {
        font-size: .95rem;
        margin-left: .5rem
    }
    .skills__container .skills__item:nth-child(odd) {
        padding-right: 1rem;
    }
    .skills__container .skills__item:nth-child(even) {
        padding-left: 1rem;
    }
    .skills__container .skills__item-label {
        margin-bottom: .5rem;
    }
    .skills__container .skills__item-bar {
        position: relative;
        width: 100%;
        height: 0.5rem;
        background: #d7e993;
        border-radius: 1rem;
    }
    .skills__container .skills__item-bar-procces {
        content:"";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background: #59a073;
        border-radius: 1rem;
    }
    @media screen and (max-width: 600px) {
        .skills__container .skills__item {
            padding: 0!important;
        }
        .skills__container .skills__item:last-child {
            margin-bottom: 0;
        }
    }
</style>


