<template>
    <div class="resume__container">
        <div class="button-close" v-on:click="redirectRouteCustom('/')"><v-icon>clear</v-icon></div>
            <div class="title__container">
                <p class="title__background">RESUME</p>
                <p class="title__content">RESUME</p>
            </div>
        <v-container fluid={false} class="home__block">
            <div class="home-background">
                <div class="home-container">
                    <AboutUs />
                    <Skills />  
                    <Experience />
                    <Education />
                    <Objective />
                </div>
            </div>
        </v-container>
    </div>
</template>

<script>
    import AboutUs from "./AboutUs"
    import Skills from "./Skills"
    import Experience from "./Experience"
    import Education from "./Education"
    import Portfolio from "./Portfolio"
    import Objective from "./Objective"
    export default {
        name: "home",
        components: {
            AboutUs,
            Skills,
            Experience,
            Education,
            Portfolio,
            Objective
        },
        data () {
            return {
                items: [
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg'
                    },
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/sky.jpg'
                    },
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/bird.jpg'
                    },
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/planet.jpg'
                    }
                ]
            }
        },
        methods: {
            redirectRouteCustom(page) {
               this.$emit("redirectRouteCustom", page)
           }
        }
    }
</script>

<style scoped>
    .resume__container {
        width: 100%;
        height: 100%;
        overflow-y: auto;
    }
    .home-background {
        background: #d2e893;
        /* height: 1500px; */
        border-radius: 20px;
        padding: 1rem;
    }
    .home-container {
        width: 100%;
        height: 100%;
        background: #ffffff;
        border-radius: 20px;
        box-shadow: 0 2px 43px -4px rgba(0,0,0,.19);
        padding: 5%;
    }
    .home-container p {
        font-family: 'Pacifico';
        font-size: 20px;
    }
</style>


