<template>
    <div class="portfolio__container">
        <div class="button-close" v-on:click="redirectRouteCustom('/')"><v-icon>clear</v-icon></div>
        <div class="title__container">
            <p class="title__background">PORTFOLIO</p>
            <p class="title__content">PORTFOLIO</p>
        </div>
        <v-container fluid={false}>
            <v-layout row  v-masonry transition-duration="0.3s" item-selector=".item" >
                <v-flex v-masonry-tile xs12 sm6 md6 lg6 class="item portfolio__item" v-for="(value) in projectsDisplay" v-bind:key="value.id">
                    <div class="portfolio__item-container">
                        <div class="portfolio__item-thumbnail-container">
                            <img class="portfolio__item-thumbnail" v-bind:src="value.thumbnail" alt="">
                        </div>
                        
                        <div class="portfolio__item-content">
                            <!-- <div class="block__title--container">
                                <p class="block__title--background">{{value.name}}</p>
                                <p class="block__title--content">{{value.name}}</p>
                            </div> -->
                            <p class="portfolio__item-name">{{value.name}}</p>
                            <div class="portfolio__item-desc" v-html="value.content"></div>
                        </div>
                    </div>
                </v-flex>
            </v-layout>
            <div class="portfolio__button">
                <div class="portfolio__item-loadmore" v-on:click="loadMore()">
                    <p>LOAD MORE</p>
                    <v-icon>expand_more</v-icon>
                </div>
            </div>

            <!-- <v-dialog v-model="dialog" width="100%">
                <v-card
                    class="mx-auto portfolio__dialog"
                    max-width="500"
                >
                    <v-img v-bind:src="dialogContent.thumbnailHeader">
                        <v-card-title class="portfolio__dialog-title">{{dialogContent.name}}</v-card-title>
                    </v-img>

                    <v-card-text class="text--primary portfolio__dialog-text" v-html="dialogContent.content">
                    </v-card-text>

                    <v-card-actions class="portfolio__dialog-action">
                        <v-btn class="portfolio__dialog-btn" v-bind:href="dialogContent.linkLive" target="_blank">LIVE</v-btn>
                        <v-btn class="portfolio__dialog-btn" v-on:click="dialog = false">CLOSE</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog> -->

        </v-container>
    </div>
</template>

<script>
export default {
  name: "PORTFOLIO",
  data() {
    return {
      dialog: false,
      dialogContent: {
          name: "",
          thumbnail: "",
          thumbnailHeader: "",
          content: "",
          linkLive: ""
      },
      startPoint: 0,
      endPoint: 5,
      projectsDisplay: [],
      projectsOrigin: [
        {
          id: 1,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.png",
          thumbnailHeader: "../static/assets/images/ajimayo_001.png",
          content: `
            <p class="portfolio__dialog-text-content"><strong>Platform</strong> Website</p>
            <p><strong>Description</strong> Website campaign  marketing</p>
            <p><strong>Scope of work</strong> Backend: create api, Front-end: Maintenance website, developing new functions, mapping api</p> 
            <p><strong>Scripting</strong> Using PHP, Mysql, Javascript, Html, Css, Vuejs</p>
          `,
          linkLive: 'https://ajimayo.com.vn/'
        },
        {
          id: 2,
          name: "Decathlon",
          thumbnail: "../static/assets/images/decathlon_001.png",
          thumbnailHeader: "../static/assets/images/decathlon_001.png",
          content: `
            <p><strong>Platform</strong> Website</p>
            <p><strong>Description</strong> Website campaign  marketing</p>
            <p><strong>Scope of work</strong>Front-end: Maintenance website, developing new functions</p> 
            <p><strong>Scripting</strong> Using Prestashop(PHP), Mysql, JQuery - Javascript, Html, Css</p>
          `,
          linkLive: 'https://www.decathlon.sg/'
        },
        {
          id: 3,
          name: "Savyu App",
          thumbnail: "../static/assets/images/ajimayo_001.png",
          thumbnailHeader: "../static/assets/images/decathlon_001.png",
          content: `
            <p><strong>Platform</strong> Website</p>
            <p><strong>Description</strong> Website campaign  marketing</p>
            <p><strong>Scope of work</strong>Front-end: Maintenance website, developing new functions</p> 
            <p><strong>Scripting</strong>Using Prestashop(PHP), Mysql, JQuery - Javascript, Html, Css</p>
          `,
        },
        {
          id: 4,
          name: "Plant Smart",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 5,
          name: "Savyu Landing Page",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 6,
          name: "Resource Management",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 7,
          name: "ProjectX",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 8,
          name: "Pulikimhung",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 9,
          name: "CauNguyen Landing Page",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 10,
          name: "CauNguyen App",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 11,
          name: "Pyramid Loyalty",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 12,
          name: "iLotusland",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        },
        {
          id: 13,
          name: "Resort International",
          thumbnail: "../static/assets/images/ajimayo_001.png"
        }
      ]
    };
  },
  created: function() {
    for (var i = this.startPoint; i <= this.endPoint; i++) {
      this.projectsDisplay.push(this.projectsOrigin[i]);
    }
  },
  methods: {
    loadMore() {
      this.startPoint = this.endPoint + 1;
      this.endPoint =
        this.endPoint + 6 > this.projectsOrigin.length - 1
          ? this.projectsOrigin.length - 1
          : this.endPoint + 6;
      console.log("this.startPoint", this.startPoint);
      console.log("this.endPoint", this.endPoint);
      for (var i = this.startPoint; i <= this.endPoint; i++) {
        this.projectsDisplay.push(this.projectsOrigin[i]);
      }
    },
    openProject(value) {
      var _this = this;
      this.dialog = true
      this.dialogContent = {
          name: value.name,
          thumbnail: value.thumbnail,
          thumbnailHeader: value.thumbnailHeader,
          content: value.content,
          linkLive: value.linkLive
      }
    },
    redirectRouteCustom(page) {
        this.$emit("redirectRouteCustom", page)
    }
  }
};
</script>

<style scoped>
    .portfolio__container {
        height: 100%;
        overflow: auto;
        /* background: #f4f7f6 */
    }
    .portfolio__item-thumbnail {
        width: 100%;
    }
    .portfolio__item {
        padding: 1rem;
    }
    .portfolio__item-container {
         transition: all 0.25s;
         background: #ffffff
    }
    /* .portfolio__item-thumbnail-container {
        overflow: hidden;
    } */
   .portfolio__item-thumbnail-container .portfolio__item-thumbnail {
        transition: all 0.25s;
        box-shadow: 0 0 28px rgba(30,30,30,.2);
    }
     /* .portfolio__item-container:hover .portfolio__item-thumbnail {
        transform: scale(1.2,1.2)
    } */
    /* .portfolio__item-container:hover {
        box-shadow: 0 0 28px rgba(30,30,30,.2);
    } */
    .portfolio__item-content {
        padding: 0.8rem 0;
    }
    .portfolio__item-content .block__title--content {
        font-size: 2rem;
        font-family: "Rubik", sans-serif;
    }
    .portfolio__item-content .block__title--background {
        font-size: 3rem;
        right: 0;
        left: 40%;
        font-family: "Rubik", sans-serif;
    }
    .portfolio__item-name {
        font-size: 2rem;
        line-height: 2rem;
        font-weight: bold;
        color: #06292f;
        font-family: "Rubik", sans-serif;
    }
    .portfolio__button {
        text-align: center;
    }
    .portfolio__button .portfolio__item-loadmore {
        padding: 0.5rem 1rem;
        cursor: pointer;
    }
    .portfolio__button .portfolio__item-loadmore i {
        font-weight: bold;
        color: #06292f;
    }
    .portfolio__button p {
        margin: 0;
        font-weight: bold;
        color: #06292f;
        padding: 0.5rem 1rem;
        border: 2px solid #d2e893;
        display: inline;
    }
    .portfolio__button .portfolio__item-loadmore p:focus {
        transform: scale(0.9,0.9)
    }
    
</style>


