<template>
    <v-layout row wrap class="portfolio__container tcd-container">
        <v-flex xs12 md4 lg4 class="portfolio__header block__header">
            <div class="block__title--container">
                <p class="block__title--background">PORTFOLIO</p>
                <p class="block__title--content">PORTFOLIO</p>
            </div>
        </v-flex>
        <v-flex xs12 md8 lg8 class="portfolio__content">
            <v-layout row>
                <v-flex xs12 md6 lg4 class="portfolio__item" v-for="(value) in projectsDisplay" v-bind:key="value.id">
                    <div class="portfolio__item-container">
                        <img class="portfolio__item-thumbnail" v-bind:src="value.thumbnail" alt="">
                        <div class="portfolio__item-name" v-on:click="openProject(value)">
                            <div class="portfolio__item-name-content">
                                <v-icon>chevron_right</v-icon>
                                <p>{{value.name}}</p>
                            </div>
                        </div>
                    </div>
                </v-flex>
                <v-flex xs12 md12 lg12 class="portfolio__item">
                    <v-btn rounded  class="portfolio__item-loadmore" v-on:click="loadMore()">
                        <p>LOAD MORE</p>
                        <v-icon>expand_more</v-icon>
                    </v-btn>
                </v-flex>
            </v-layout>
        </v-flex>

        <v-dialog v-model="dialog" width="100%">
            <v-card
                class="mx-auto portfolio__dialog"
                max-width="500"
            >
                <v-img v-bind:src="dialogContent.thumbnailHeader">
                    <v-card-title class="portfolio__dialog-title">{{dialogContent.name}}</v-card-title>
                </v-img>

                <v-card-text class="text--primary portfolio__dialog-text" v-html="dialogContent.content">
                </v-card-text>

                <v-card-actions class="portfolio__dialog-action">
                    <v-btn class="portfolio__dialog-btn" v-bind:href="dialogContent.linkLive" target="_blank">LIVE</v-btn>
                    <v-btn class="portfolio__dialog-btn" v-on:click="dialog = false">CLOSE</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

    </v-layout>
</template>

<script>
export default {
  name: "PORTFOLIO",
  data() {
    return {
      dialog: false,
      dialogContent: {
          name: "",
          thumbnail: "",
          thumbnailHeader: "",
          content: "",
          linkLive: ""
      },
      startPoint: 0,
      endPoint: 5,
      projectsDisplay: [],
      projectsOrigin: [
        {
          id: 1,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg",
          thumbnailHeader: "../static/assets/images/ajimayo_001.png",
          content: `
            <p class="portfolio__dialog-text-content"><strong>Platform:</strong> Website</p>
            <p><strong>Description:</strong> Website campaign  marketing</p>
            <p><strong>Scope of work:</strong> Backend: create api, Front-end: Maintenance website, developing new functions, mapping api</p> 
            <p><strong>Scripting:</strong>Using PHP, Mysql, Javascript, Html, Css, Vuejs</p>
          `,
          linkLive: 'https://ajimayo.com.vn/'
        },
        {
          id: 2,
          name: "Decathlon",
          thumbnail: "../static/assets/images/decathlon_001.jpg",
          thumbnailHeader: "../static/assets/images/decathlon_001.png",
          content: `
            <p><strong>Platform:</strong> Website</p>
            <p><strong>Description:</strong> Website campaign  marketing</p>
            <p><strong>Scope of work:</strong>Front-end: Maintenance website, developing new functions</p> 
            <p><strong>Scripting:</strong>Using Prestashop(PHP), Mysql, JQuery - Javascript, Html, Css</p>
          `,
          linkLive: 'https://www.decathlon.sg/'
        },
        {
          id: 3,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 4,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 5,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 6,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 7,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 8,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 9,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 10,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 11,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 12,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        },
        {
          id: 13,
          name: "Ajimayo",
          thumbnail: "../static/assets/images/ajimayo_001.jpg"
        }
      ]
    };
  },
  created: function() {
    for (var i = this.startPoint; i <= this.endPoint; i++) {
      this.projectsDisplay.push(this.projectsOrigin[i]);
    }
  },
  methods: {
    loadMore() {
      this.startPoint = this.endPoint + 1;
      this.endPoint =
        this.endPoint + 6 > this.projectsOrigin.length - 1
          ? this.projectsOrigin.length - 1
          : this.endPoint + 6;
      console.log("this.startPoint", this.startPoint);
      console.log("this.endPoint", this.endPoint);
      for (var i = this.startPoint; i <= this.endPoint; i++) {
        this.projectsDisplay.push(this.projectsOrigin[i]);
      }
    },
    openProject(value) {
      var _this = this;
      this.dialog = true
      this.dialogContent = {
          name: value.name,
          thumbnail: value.thumbnail,
          thumbnailHeader: value.thumbnailHeader,
          content: value.content,
          linkLive: value.linkLive
      }
    }
  }
};
</script>

<style scoped>
.portfolio__container .portfolio__content .row {
  margin: 0 -0.5rem;
}
.portfolio__item {
  padding: 0 0.5rem;
  margin-bottom: 1rem;
  animation: fadeIn 0.5s;
}
.portfolio__item-container {
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  background: #ececec;
  cursor: pointer;
}
.portfolio__item-loadmore {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: tomato !important;
  box-shadow: 3px 3px 6px #0000001d;
  border-radius: 1.5rem;
  padding: 0.75rem 1rem;
  width: 250px;
  margin: auto;
  position: relative;
}
.portfolio__item-loadmore p {
  color: #ffffff;
  font-size: 1rem;
  font-weight: bold;
  margin: 0;

  display: inline;
}
.portfolio__item-loadmore .v-icon {
  background: #ffffff;
  color: tomato;
  border-radius: 50%;
  position: absolute;
  right: 0;
}

/* .portfolio__item:nth-child(odd) {
        padding-right: .5rem;
    }
    .portfolio__item:nth-child(even) {
        padding-left: .5rem;
    } */
.portfolio__item-thumbnail {
  width: 100%;
  border-radius: 1rem;
  transition: all 0.5s;
  transform: scale(0.9, 0.9);
  box-shadow: 3px 3px 6px #0000001d;
}
.portfolio__item-name {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(108, 108, 229, 0.7);
  opacity: 0;
  transition: all 0.5s;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.portfolio__item-name-content {
  text-align: center;
}
.portfolio__item-name p {
  color: #ffffff;
  font-weight: bold;
  opacity: 1;
  transition: all 0.7s;
}
.portfolio__item-container .v-icon {
  padding: 0.5rem;
  border-radius: 50%;
  background: gold;
  margin-bottom: 0.5rem;
  margin-top: 1.5rem;
  /* position: absolute;
        bottom: 1rem;
        left: 1rem;; */
}
.portfolio__item-container:hover .portfolio__item-thumbnail {
  transform: scale(1, 1);
  box-shadow: none;
}
.portfolio__item-container:hover .portfolio__item-name {
  opacity: 1;
}
/* .portfolio__item-container:hover .portfolio__item-name p, */
.portfolio__item-container:hover .v-icon {
  margin-top: 0rem;
  opacity: 1;
}

.portfolio__dialog-title {
    position: absolute;
    top: 1rem;
    left: 1rem;
    font-size: 1rem;
    background: tomato;
    color: #ffffff;
    font-weight: bold;
    margin: 0;
    padding: .5rem 1rem;
    box-shadow: 3px 3px 6px #0000001d;
}
.portfolio__dialog .portfolio__dialog-text {
    padding: 1rem;
}
.portfolio__dialog .portfolio__dialog-text .portfolio__dialog-text-content  {
    margin-bottom: 0!important;
}
.portfolio__dialog .portfolio__dialog-action {
    display: flex;
    justify-content: flex-end;
}
.portfolio__dialog .portfolio__dialog-action .portfolio__dialog-btn {
    padding: .5rem 1rem;
    margin-left: .5rem;
    border-radius: 1rem;
    text-decoration: none;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>


