<template>
    <div class="home--container">
        <div class="home--background"></div>
        <div class="home--menu">
            <a data-aos="fade-right" data-aos-delay="100" v-on:click="redirectRouteCustom('/slider')"><span>RESUME</span></a>
            <a data-aos="fade-right" data-aos-delay="200" v-on:click="redirectRouteCustom('/portfolio')"><span data-go="/portfolio" class="page-toggle">PORTFOLIO</span></a>
            <a data-aos="fade-right" data-aos-delay="300" v-on:click="redirectRouteCustom('/contact')"><span>CONTACT</span></a>
            <!-- <router-link to="/slider">Go to Foo</router-link> -->
        </div>
        <div class="home--header">
            <div class="home--header-top" data-aos="fade-left" data-aos-delay="3000">Developer</div>
            <div class="home--header-center">
                <!-- <img class="home--logo" src="/static/assets/logo.png" alt=""> -->
                <div class="home--logo"><img class="home--logo" src="/static/assets/logo.png" alt=""></div>
                <div class="home--name"><p>Trinh Cong Danh</p></div>
            </div>
            <marquee class="home--header-bottom" data-aos="fade-left" data-aos-delay="3000">Building creative websites for the world's people. Always in search
                    of the great user experience.</marquee>
        </div>
    </div>
</template>

<script>
    // import AboutUs from "./AboutUs"
    // import Skills from "./Skills"
    // import Experience from "./Experience"
    // import Education from "./Education"
    // import Portfolio from "./Portfolio"
    export default {
        name: "home",
        components: {
            // AboutUs,
            // Skills,
            // Experience,
            // Education,
            // Portfolio
        },
        data () {
            return {
                x: 0,
                y: 0,
                lFollowX: 0,
                lFollowY: 0,
                friction: 1 / 30
            }
        },
        created() {
           
        },
        methods: {
            moveBackground() {
                this.x += (this.lFollowX - this.x) * this.friction;
                this.y += (this.lFollowY - this.y) * this.friction;
                
                var translate = 'translate(' + this.x + 'px, ' + this.y + 'px) scale(1.1)';

                $('.home--background').css({
                    '-webit-transform': translate,
                    '-moz-transform': translate,
                    'transform': translate
                });

                window.requestAnimationFrame(this.moveBackground);
           },
           redirectRouteCustom(page) {
               this.$emit("redirectRouteCustom", page)
           }
        },
        mounted() {

            var lFollowX = this.lFollowX,
                lFollowY = this.lFollowY,
                x = this.x,
                y = this.y,
                friction = this.friction,
                _this = this;

            $(window).on('mousemove click', function(e) {

                var lMouseX = Math.max(-100, Math.min(100, $(window).width() / 2 - e.clientX));
                var lMouseY = Math.max(-100, Math.min(100, $(window).height() / 2 - e.clientY));
                _this.lFollowX = (20 * lMouseX) / 100; // 100 : 12 = lMouxeX : lFollow
                _this.lFollowY = (10 * lMouseY) / 100;
                
            });
            _this.moveBackground();
           
        }
    }
</script>

<style scoped>
    .home--background {
        width: 100%;
        height: 100%;
        
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        
        background: url('https://images.wallpapersden.com/image/download/forest-minimal_57638_3840x2160.jpg') no-repeat center center;
        background-size: cover;

        transform: scale(1.1);
    }
    .home--menu {
        position: absolute;
        top: 50%;
        left: 0%;
        transform: translate(0, -50%);
    }
    .home--menu a { 
        font-size: 2rem;
        line-height: 1;
        position: relative;
        outline: none;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin: 1.8vh 0;
        overflow: hidden;
        text-decoration: none;
        text-transform: uppercase;
        font-weight: bold;
        position: relative;
        margin: 0;
        cursor: pointer;
    }
    .home--menu span {
        font-family: 'Anton';
        font-size: 5.6vh;
        font-weight: 400;
        padding: 0.5rem 1rem;
        position: relative;
        color: #ffffff;
        transition: all 0.25s linear;
    }
    .home--menu span::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background: #d2e893;
        width: 0%;
        height: 100%;
        z-index: -1;
        transition: all 0.25s linear;
    }
    .home--menu span:hover::after {
        width: 100%;
        box-shadow: 3px 3px 8px #00000021;
        
    }
    .home--menu span:hover {
        color: #06292f
    }
    /* home--header */
    .home--header {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 30%;
        transform: translate(-50%, -50%);
    }
    .home--header .home--header-top {
        text-align: right;
        color: #ffffff;
        font-weight: bold;
        text-transform: uppercase;
        font-family: "Rubik", sans-serif;
        font-weight: bold;
        font-size: calc(11px + .14vw);
    }
    .home--header .home--header-bottom {
        color: #ffffff;
        font-size: calc(12px + .14vw);
        font-family: "Rubik", sans-serif;
        font-weight: 400;
    }
    .home--header .home--header-center {
        display: flex;
        width: 100%;
        margin: 1% 0 0.5%;
    }
   
    .home--header .home--header-center .home--name {
        background-color: #06282f;
        padding: 0.5rem 0rem;
        overflow: hidden;
        flex: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        animation: HOME_NAME 1s 3s;
        animation-fill-mode: forwards;
        -webkit-animation-fill-mode: forwards;
    }
    .home--header .home--header-center .home--name p {
        font-family: "Rubik", sans-serif;
        font-weight: 400;
        color: #ffffff;
        font-size: 3vw;
        margin: 0;
        white-space: nowrap;
    }
    .home--header .home--header-center .home--logo {
        width: 20%;
        opacity: 0;
        transform: translate(250%, 0%);
        animation: HOME_LOGO 2s 1s;
        animation-fill-mode: forwards;
        -webkit-animation-fill-mode: forwards;
    }
    .home--header .home--header-center .home--logo img {
        width: 100%;
        align-self: center;
    }
    @keyframes HOME_NAME {
        from {
            flex: 0
        }
        to {
            flex: 1
        }
    }
    @keyframes HOME_LOGO {
        0% {
            opacity: 0;
            transform: translate(250%, 0%);
        }
        50% {
            opacity: 1;
            transform: translate(250%, 0%);
        }
        100% {
            opacity: 1;
            transform: translate(0%, 0%);
        }
    }
    @media screen and (max-width: 1024px) {
        .home--header {
            position: absolute;
            top: 1%;
            left: 1rem;
            max-width: 400px;
            min-width: 200px;
            width: 50%;
            transform: translate(0%, 0%);
        }
        .home--header .home--header-top,
        .home--header .home--header-bottom {
            color: #06292f
        }
    }
</style>


