<template>
    <div class="contact__container">
        <div class="button-close" v-on:click="redirectRouteCustom('/')"><v-icon>clear</v-icon></div>
        <div>
            <div class="title__container">
                <p class="title__background">CONTACT</p>
                <p class="title__content">CONTACT</p>
            </div>
        </div>
        <div class="contact__container-background"></div>
        <v-container fluid={false} class="contact__content-container">
            <v-layout row>
                <v-flex xs12 md12 lg12 class="contact__item">
                    <div class="contact__item-info">
                        <div class="contact__item-container">
                            <v-icon>place</v-icon>
                            <div class="contact__item-content">
                                <p class="contact_item-title">Location</p>
                                <p class="contact_item-desc">District 5 , Ho Chi Minh City</p>
                            </div>
                        </div>
                        <div class="contact__item-container">
                            <v-icon>email</v-icon>
                            <div class="contact__item-content">
                                <p class="contact_item-title">Email</p>
                                <p class="contact_item-desc">trinhdanh04@gmail.com</p>
                            </div>
                        </div>
                        <div class="contact__item-container">
                            <v-icon>call</v-icon>
                            <div class="contact__item-content">
                                <p class="contact_item-title">Location</p>
                                <p class="contact_item-desc">+84 909703296</p>
                            </div>
                        </div>
                    </div>
                    
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
    export default {
        name: "home",
        components: {
        },
        data () {
            return {
                items: [
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg'
                    },
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/sky.jpg'
                    },
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/bird.jpg'
                    },
                    {
                        src: 'https://cdn.vuetifyjs.com/images/carousel/planet.jpg'
                    }
                ]
            }
        },
        methods: {
            redirectRouteCustom(page) {
               this.$emit("redirectRouteCustom", page)
           }
        }
    }
</script>

<style scoped>
    .contact__container .title__container {
        margin-bottom: 0;
    }
    .contact__container-background {
        background: url('https://www.xpotential.co.uk/wp-content/uploads/2018/01/map-background.jpg') no-repeat center center;
        z-index: -1;
        opacity: 0.2;
        background-size: cover;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .contact__container {
        display: flex;
	    flex-direction: column;
        width: 100%;
        height: 100%;
        overflow-y: auto;
    }
    .contact__content-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
    }
    .contact__item {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 1rem;
    }
    .contact__item-info {
        width: 100%;
    }
    .contact__item .contact__item-container {
        /* box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1); */
        /* border-radius: 10%; */
        /* padding: 10% 0 ; */
        width: 100%;
        margin: 5% 0;
        position: relative;
    }
    .contact__item i {
        /* position: absolute;
        top: 50%;
        left: 50%;
        
        z-index: -1; */
        /* transform: translate(-50%,-50%); */
        font-size: 7rem;
        color: #e9e9e9;
    }
    .contact__item .contact__item-content {
        position: absolute;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .contact__item .contact_item-title {
        font-size: 2rem;
        font-weight: bold;
        margin-bottom: 0rem;
    }
    .contact__item .contact_item-desc {
        margin-bottom: 0rem;
    } 
</style>


